 import React from 'react';
 import './burg.css'
 import BurgerSkladniki from './burgerSkladniki'


const burger=(props)=>
{    
    let transformedSkladniki=  Object.keys(props.skladniki).map(element=>
        {   
                  console.log(element)  //tutaj podajemy element jako odniesienie do obiektu za pomoca[];
           return [...Array(props.skladniki[element])].map((_,i)=>
             {  
                
                return   <BurgerSkladniki key={element+i} type={element}/>  
              
             })
       })
       
       const checkingTab=transformedSkladniki.flat()
      
       if(checkingTab.length===0)
       {
            transformedSkladniki=<p>Twoj Burger jest pusty :(</p>
       }
    
       console.log(transformedSkladniki);
  
    return (
    <div className={'Burger'}>
    <BurgerSkladniki type='bread-top'/>
    {transformedSkladniki}
    <BurgerSkladniki type='bread-bottom'/>
    </div>);
}



export default burger
