import React from 'react';
import './logo.css'
import BurgerLogo from '../../Img/burger-logo.png';
const Logo=(props)=>
(
 <div className='Logo' 
     style={{height:props.height}}> 
     <img src={BurgerLogo}
      onClick={props.clicking}
      alt='burger' 
      className='img'
      />
 </div>
)

export default Logo 