 import React from 'react';
 import './kontrolki.css';
 import Kontrol from './kontrol/kontrol.js'

 const ctrlElm=
 [
   {label:'Salad',type:'salad'},
   {label:'Bacon',type:'bacon'},
   {label:'Cheese',type:'cheese'},
   {label:'Meat',type:'meat'}
 ]
 

 const controlls=(props)=>
 {
   return <div className='kontrolki'> 
       <p>Cena Hamburgera wynosi {props.price} zl</p>
     {ctrlElm.map(ctrl=>
      {
        return <Kontrol 
        key={ctrl.label} 
        text={ctrl.label}
        dodane={()=>props.dodaneSkladniki(ctrl.type)} 
        odjete={()=>props.odjeteSkladniki(ctrl.type)}
        disable={props.checkButt[ctrl.type]}/>
      })}
      <button className={'OrderButton'} onClick={props.checkorder}
      disabled={!props.zamowienie}>Zamow teraz</button>
    </div>
 }

 export default controlls