import React from 'react';
import Nav from '../nav/navitems/nav.js'
import Logo from '../logo/logo.js'
import './side.css';
import Backdrop from '../Burger/UI/Backdrop/backdrop.js'

const Side=(props)=>
{
    let classTab=['side','close']
    if(props.open)
    {
        classTab=['side','open'];
    }
return <>
<Backdrop show={props.open} clicked={props.click}/>

 <div className={classTab.join(' ')}>

   <Logo height={'11% '} 
   />
  <nav>
      <Nav/>
  </nav>
 </div>
 </>
}

export default  Side