import React,{Component} from 'react';
import './burger.css'
import propTypes from 'prop-types';

class BurgerSkladniki extends  Component{
    render()
    {
        let skladniki=null
        switch(this.props.type)
        {
          case('bread-bottom'):
      
            skladniki= <div className={'BreadBottom'}> </div>;
            break;
      
          case('bread-top'):
           skladniki=(
                <div className={'BreadTop'}>
                <div className={'Seeds1'}></div>
                <div className={'Seeds2'}></div>
               </div>
               );
               break;
      
          case('meat'):
          skladniki= <div className={'Meat'}></div>
          break;
      
          case('cheese'):
          skladniki= <div className={'Cheese'}></div>
          break;
      
          case('bacon'):
          skladniki= <div className={'Bacon'}></div>
          break;
      
          case('salad'):
          skladniki= <div className={'Salad'}></div>
          break;
           
          default:
          skladniki=null;
           
         
        }
        return skladniki
    }
}

BurgerSkladniki.propTypes={
     type:propTypes.string.isRequired
};

 



export default BurgerSkladniki;