 import React from 'react'
import './order.css'


const orderSummary=(props)=>
{   

        const skladniki=Object.keys(props.skladniki).map(element=>
            {
                return <li key={element}>
                <span>{element} </span>
                {props.skladniki[element]}
                </li>
         })  
            
  return <>
        <h3>Twoje zamówienie</h3>
         <p>Wspanialy burger z: </p>
     <ul> 
       {skladniki}
     </ul>
     <p>
       <strong>
       Cena wynosi: {props.price} zł
       </strong>
       </p>
     <p>Kontynuować?</p>
      <div className={'divEl'}>
       <button className={'Button Danger'}
       onClick={props.CloseiT}
       ><strong>Nie</strong>
       </button>
       <button 
       className={'Button Success'}
       onClick={props.yesBut}>
       <strong>Tak</strong>
       </button>
       </div>

     

  </>
}

export default orderSummary