import React from 'react';
import './kontrol.css' 

const controll=(props)=>
( 
   <div className='BuildControl'>
    <div className='Label'>{props.text}</div>
    <button className='Less'  onClick={props.dodane }>Dodaj</button>
    <button 
     className='More' onClick={props.odjete} disabled={props.disable}>Odejmij</button>
   </div>
)

export default controll