 import React,{Component} from 'react';
 import  './uklad.css'
 import Toolbar from '../components/nav/toolBar/toolbar.js'
 import Side from '../components/sideDrawer/SideDraw.js'


class Layout extends Component
{
  state=
  {
   showSide:true,
  }

  closeSide=()=>
  {
    this.setState({showSide:false})
  }
  addSide=()=>
  {
    this.setState({showSide:true})
    console.log('ifewfwfewfdsfs')
  }


  render()
  {
    return (
      <>
     <Toolbar
      DoSide={this.addSide} 
       ShowSide={this.addSide}/>
     
     <Side  click={this.closeSide}
      open={this.state.showSide}
      />
     <main className='uklad'>
     {this.props.children}
     </main>
   </>
  )

  }
}
 

 export default Layout;