import React, { Component } from 'react';
import Burger from '../components/Burger/burger'
import BurgCtrl from "../components/Burger/burger.kontrolki/kontrolki";
import Modal from '../components/Burger/UI/Modal/modal.js'
import OrderSummary from '../components/Burger/orderSummary/orderSummary.js'
import axios from '../axios/axios.js'
import Loading from '../components/Burger/UI/spinner/spinner.js'


const cenyProduktow=
{
    salad:0.5,
    bacon:1,
    cheese:1,
    meat:2
}
class BurgerBild extends Component {

 state={
     skladniki:null,
     totalPrice:6,
     doZamowienia:false,
     showOrder:false,
     loading:false,
    
 }
 componentDidMount()
 {
     axios.get('https://burgerqueat.firebaseio.com/test/Skladniki.json')
     .then(response=>
        {
            this.setState({skladniki:response.data})
            console.log(response.data);
        })
 }
 checkZamowienie=(aktualneSkladniki)=>
 {
   
     const sum=Object.keys( aktualneSkladniki).map(igKey=>
        {
            return  aktualneSkladniki[igKey];
        })
      const wyn=sum.reduce((acc,su)=>acc + su,0);
      
      this.setState({doZamowienia:wyn>0});

 }
 checkOrder=()=>
 {
   const checkIF=this.state.showOrder;
   this.setState({showOrder:!checkIF})
 }
 closeIT=()=>
 {
    this.setState({showOrder:false})
 }
 butCont=()=>
 {
    const order=
    {
        ingriediens:this.state.skladniki,
        price:this.state.totalPrice,
        customer:
        {
            name:'Kuba',
             adress:
             {
                 street:'wyszyn',
                 country:'POLSKA'
             },
             email:'Test@test',
        },
        deliveryMetod:'Fastest!!!'
    }
   axios.post('/test.json', order)
   .then(response=>
       {
        this.setState({loading:false,doZamowienia:false})
       })
   .catch(error=>
    {
        this.setState({loading:false,doZamowienia:false});
    });
   
   this.setState({loading:true})
 }

 dodajSkladniki=(type)=>
 {
    const before=this.state.skladniki[type];
    const newCount=before+1;
    console.log(before);
    const aktualneSkladniki={...this.state.skladniki};
    aktualneSkladniki[type]=newCount;
   
    const prices=cenyProduktow[type]
    const oldPrice=this.state.totalPrice;
    const newPrice=oldPrice+prices;
    this.setState({totalPrice:newPrice, skladniki:aktualneSkladniki})
    this.checkZamowienie( aktualneSkladniki)



 }

 usunSkladnik=(type)=>
 {
  const before=this.state.skladniki[type];
  const after=before-1;
  console.log(after)
  const aktualneSkladniki={...this.state.skladniki};
  aktualneSkladniki[type]=after;


    const prices=cenyProduktow[type]
    const oldPrice=this.state.totalPrice;
    const newPrice=oldPrice-prices;
    if(after>=0) this.setState({totalPrice:newPrice, skladniki:aktualneSkladniki})
    this.checkZamowienie( aktualneSkladniki)
 }
 

render()
{ 
    const checkButton={...this.state.skladniki}
    for(let element in checkButton)
    {
        checkButton[element]=checkButton[element]<=0
        console.log(checkButton)
    }
    let orderSummary=null
  
    let burger=<Loading />
    
    if(this.state.skladniki)
    {
        burger= (
            <>
           <Burger skladniki={this.state.skladniki}/>
           <BurgCtrl dodaneSkladniki={this.dodajSkladniki}  checkorder= {this.checkOrder}
           odjeteSkladniki={this.usunSkladnik} 
           checkButt={checkButton}
           zamowienie={this.state.doZamowienia}
           price={this.state.totalPrice}/>
           </>)
            orderSummary= <OrderSummary 
            skladniki={this.state.skladniki} 
            yesBut={this.butCont}
            CloseiT={this.closeIT}
            price={this.state.totalPrice}/>
    }
    if (this.state.loading)
    {
        orderSummary= <Loading/>
    }

    return (<>
      <Modal showIt={this.state.showOrder}
      CloseModal={this.closeIT}>
       {orderSummary}
      </Modal>
      {burger}
     
     </>)
}

}

export default BurgerBild;


