import React from 'react';
import './modal.css';
import Backdrop from '../Backdrop/backdrop.js'

const modal =(props)=>
{
  let claser='Modal'

  if(!props.showIt)
  {
     claser='Modal active'
  }
  return <> 
  <Backdrop 
  show={props.showIt}
  clicked={props.CloseModal}/>
  <div className={claser}>
   {props.children}
   </div> </>
  
}

export default modal